import React from 'react';
import { render } from "react-dom"
import { Link} from 'react-router-dom';
import LeadAuditComponent from '../component/LeadAuditComponent';
import FlowContainer from "../../common/container/core/FlowContainer"
import { Card,Modal } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { viewLead } from '../../../actions/lead_actions';
import { approveKYC } from '../../../actions/kyc_actions';
import {Redirect} from "react-router-dom";
import {check_priv} from "../../../helpers/storage_helper";
import {dd_value} from "../../../actions/common_actions";
import { updateLeadStatus } from '../../../actions/kyc_actions';



class LeadAuditContainer extends FlowContainer
{
    state = { lead: {},
              to_edit: false,
              modalBox:false,
              reassign_lead :{},
              view_cust_id: false
            }


    componentWillMount()
    {
        this.id = this.props.match.params.id;
        
        viewLead(this.req({id: this.id}))
        .then((response) => 
        {
            this.resp(response)
            if(!response){return };
            if(response.status === "success")
            {

                let lead = response.data
                let view_cust_id = false;

                if(lead.cust_reg_json.cust_id){
                    if((lead.type == 'kyc' && lead.status >= '59') ||
                           lead.type == 're_kyc'){
                        view_cust_id = true;
                    }
                }
                this.setState({lead, view_cust_id})
            }
        })
        
    }

    validate_fields(fields) {
        if(fields.remarks && fields.reassign_reason){
             return true
        }else{
            alert("Please enter required fields")
        }
        return false   
    }

    handleKYCEdit = (event) =>{
        const lead = this.state.reassign_lead
        lead.lead_id =  this.id 
        var valid = this.validate_fields(lead)
        if(valid){
            updateLeadStatus(this.req(lead))
                .then((response) => {
                    this.resp(response)
                    if(!response){return };
                    if(response.status === "success")
                    {
                        alert(response.message);
                        this.setState({to_edit: true})
                    }
            })
        }
    }

    handleModalOpen = (event) => {
        this.setState({modalBox:true})
    }    

    handleAudit = (event) => {
        event.preventDefault();
        var borrower = this.state.lead.cust_reg_json;
        borrower.lead_id = this.state.lead.id;
        borrower.flow_rel_mgr_id = this.state.lead.flow_rel_mgr_id;
        borrower.acc_purpose = this.state.lead.acc_purpose;
        approveKYC(this.req({lead_id: this.state.lead.id}, true))
        .then((response) => 
        {
            this.resp(response)
            if(!response){return};
            if(response.status === "success")
            {
              this.setState({cust_id: response.data});
              this.setState({toList: true});
                alert(response.message);
                window.open(`/borrower/indiv/view/${this.state.lead.cust_reg_json.cust_id}`, "_blank")
                this.setState({to_edit: true})
            }
            else
            {  
                alert(response.message + "\n" + JSON.stringify(response.data));  
            }
        })
    }

   

       
    render()
    { 
        if(!check_priv("lead","audit_kyc")){
            return <Redirect to='/'/>
        }
        return(<>
            {this.state.to_edit ?
            <Redirect to={`/lead/edit/${this.props.match.params.id}`}/> :
            
            <>
             
            <div className="container " style = {{maxWidth :"1500px"}}>
                <Card>
                    <Card.Header style = {{backgroundColor: "#01062c", color: "white"}}>
                        <h4 className="mb-0">{this.props.title}</h4>
                    </Card.Header>
                    <Card.Header>
                        <div className="d-flex justify-content-between">
                            <h4 style={{ fontSize: "18px" }}>Type : {dd_value(this.state.lead.type, 'lead_type')}</h4>

                            <h4 style={{ fontSize: "18px" }}>Status : {dd_value(this.state.lead.status)}</h4>

                        </div>
                    </Card.Header>
                    <Card.Header style = {{color: "white"}}>
                        <div className="d-flex justify-content-between">

                            {this.state.view_cust_id ?
                                <h4 style={{ fontSize: "18px" }}>Cust ID : <Link to={"/borrower/indiv/view/"+this.state.lead.cust_reg_json.cust_id} target="_blank" ><span style={{ fontSize: "18px" }}>{this.state.lead.cust_reg_json.cust_id}</span></Link></h4>
                                : <div></div>}

                            {this.state.lead.type == 're_kyc' &&
                            <h4 style={{ fontSize: "18px" }}>Reason : {dd_value(this.state.lead.kyc_reason)}</h4>}

                        </div>
                    </Card.Header>

                    <Card.Body>
                        <LeadAuditComponent  lead_data = {this.state.lead} submit_btn = {this.state.calling_api} handleKYCEdit = {this.handleKYCEdit} lead_id ={this.id}  id = "reassign_lead" modal = {this.state.modalBox} acc_purpose = {this.state.lead.acc_purpose} data = {this.state.lead.cust_reg_json} onComponentChange={this.handleComponentChange} />
                        {this.state.lead.status === "50_pending_audit" &&
                        <div className="form-group row reset-margin justify-content-center py-4">
                            <Button className= "btn btn-primary" disabled = {this.state.calling_api} onClick={this.handleModalOpen}>Assign to RM</Button>
                            <Button className= "btn btn-primary ml-5" disabled = {this.state.calling_api} onClick={this.handleAudit}>Approve KYC</Button>

                        </div>
                        }
                           
                    </Card.Body>
                </Card>
            </div>
            </>
        }
        </>
        )
    }
}

export default LeadAuditContainer;

